:root {
  --#{$prefix}color-background-0: #{map-get($palette, 'white')};
  --#{$prefix}color-background-1: ${Palette.gray100};
  --#{$prefix}color-background-dimmed: ${Palette.dimmed};
  --#{$prefix}color-surface-primary: ${Palette.orange500};
  --#{$prefix}color-surface-secondary: ${Palette.gray900};
  --#{$prefix}color-surface-neutral: ${Palette.white};
  --#{$prefix}color-surface-neutral-1: ${Palette.gray100};
  --#{$prefix}color-surface-neutral-2: ${Palette.gray200};
  --#{$prefix}color-surface-neutral-3: ${Palette.gray300};
  --#{$prefix}color-text-primary: ${Palette.orange500};
  --#{$prefix}color-text-body: ${Palette.black};
  --#{$prefix}color-text-body-1: ${Palette.gray900};
  --#{$prefix}color-text-body-2: ${Palette.gray800};
  --#{$prefix}color-text-body-3: ${Palette.gray700};
  --#{$prefix}color-text-body-0: ${Palette.white};
  --#{$prefix}color-text-neutral-1: ${Palette.gray600};
  --#{$prefix}color-text-neutral-2: ${Palette.gray500};
  --#{$prefix}color-border-1: ${Palette.gray100};
  --#{$prefix}color-border-2: ${Palette.gray200};
  --#{$prefix}color-border-3: ${Palette.gray300};
  --#{$prefix}color-border-4: ${Palette.black};
  --#{$prefix}color-additional-semantic-1: ${Palette.deepBlue500};
  --#{$prefix}color-additional-semantic-2: ${Palette.red500};
  --#{$prefix}color-additional-semantic-3: ${Palette.deepYellow500};
  --#{$prefix}color-additional-semantic-4: ${Palette.gray500};
  --#{$prefix}color-additional-custom-1: ${Palette.indigoBlue400};
  --#{$prefix}color-additional-custom-2: ${Palette.orangeEmber400};
  --#{$prefix}color-additional-custom-3: ${Palette.deepYellow300};
  --#{$prefix}border-radius-small: 4px;
  --#{$prefix}border-radius-medium: 8px;
  --#{$prefix}border-radius-large: 12px;
  --#{$prefix}border-radius-xlarge: 16px;
  --#{$prefix}border-radius-xxlarge: 100px;
  --#{$prefix}box-shadow-small: 0px 5px 8px rgb(0 0 0 / 2%);
  --#{$prefix}box-shadow-medium: 0px 1px 3px rgb(0 0 0 / 8%);
  --#{$prefix}box-shadow-large: 0px 2px 12px rgb(0 0 0 / 6%);
  --#{$prefix}spacing-xxxsmall: 0.25rem;
  --#{$prefix}spacing-xxsmall: 0.5rem;
  --#{$prefix}spacing-xsmall: 0.75rem;
  --#{$prefix}spacing-small: 1rem;
  --#{$prefix}spacing-medium: 1.25rem;
  --#{$prefix}spacing-large: 1.5rem;
  --#{$prefix}spacing-xlarge: 1.75rem;
  --#{$prefix}spacing-xxlarge: 2rem;
  --#{$prefix}spacing-xxxlarge: 3rem;
  --#{$prefix}font-size-title-xsmall: 1rem;
  --#{$prefix}font-size-title-small: 1.125rem;
  --#{$prefix}font-size-title-medium: 1.25rem;
  --#{$prefix}font-size-title-large: 1.375rem;
  --#{$prefix}font-size-title-xlarge: 1.625rem;
  --#{$prefix}font-size-text-xsmall: 0.75rem;
  --#{$prefix}font-size-text-small: 0.875rem;
  --#{$prefix}font-size-text-medium: 1rem;
  --#{$prefix}font-weight-light: 300;
  --#{$prefix}font-weight-regular: 400;
  --#{$prefix}font-weight-bold: 700;
  --#{$prefix}font-line-height-medium: 1.4;
  --#{$prefix}font-line-height-large: 1.5;
  --#{$prefix}z-index-level-1: 1;
  --#{$prefix}z-index-level-2: 2;
  --#{$prefix}z-index-level-3: 3;
  --#{$prefix}z-index-level-4: 4;
  --#{$prefix}z-index-level-5: 5;
  --#{$prefix}z-index-level-6: 6;
  --#{$prefix}z-index-level-7: 7;
  --#{$prefix}z-index-modal: 1000;
  --#{$prefix}z-index-spinner: 2000;
}
